import * as actionTypes from "./actionTypes";

export const decrementClickedHandler = lengthID => {
  switch (true) {
    case lengthID === "break-length":
      return {
        type: actionTypes.DECREMENT_CLICKED,
        lengthName: "break"
      };
    case lengthID === "session-length":
      return {
        type: actionTypes.DECREMENT_CLICKED,
        lengthName: "session"
      };
    default:
      console.log("decrementClickedHandler default triggered");
  }
};
