import React from "react";
import styles from "./TimerControls.module.css";

import { connect } from "react-redux";

import * as actions from "../../store/actions/actions";

const TimerControls = props => {
  const startStopTimerClearInterval = displayText => {
    switch (displayText) {
      case "Session":
        clearInterval(props.timerSession);
        clearInterval(props.timerBreak);
        break;
      case "Break":
        clearInterval(props.timerSession);
        clearInterval(props.timerBreak);
        break;
      default:
        console.log("startStopTimerClearInterval default triggered");
    }
  };

  return (
    <div className={styles.TimerControls}>
      <button
        id="start_stop"
        className={styles.p}
        onClick={() => {
          props.startStopClickedHandler(props.displayText, props.timerRunning);
          if (props.timerRunning) {
            startStopTimerClearInterval(props.displayText);
          }
        }}
      >
        start_stop
      </button>
      <button
        id="reset"
        className={styles.p}
        onClick={() => {
          startStopTimerClearInterval(props.displayText);
          document.getElementById("beep").pause();
          document.getElementById("beep").currentTime = 0;
          props.resetClickedHandler();
        }}
      >
        reset
      </button>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    displayText: state.displayText,
    timerRunning: state.timerRunning,
    timerSession: state.timerSession,
    timerBreak: state.timerBreak
  };
};

const mapDispatchToProps = dispatch => {
  return {
    startStopClickedHandler: (displayText, timerRunning) =>
      dispatch(actions.startStopClickedHandler(displayText, timerRunning)),
    resetClickedHandler: () => dispatch(actions.resetClickedHandler())
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TimerControls);
