import * as actionTypes from "./actionTypes";

export const startStopClickedHandler = (displayText, timerRunning) => {
  switch (displayText) {
    case "Session":
      switch (timerRunning) {
        case false:
          return dispatch => {
            const timerSession = setInterval(
              () => dispatch({ type: actionTypes.SESSION_TIMER_TICK }),
              1000
            );
            dispatch({
              type: actionTypes.SESSION_TIMER_START,
              timerSession: timerSession
            });
            // dispatch({ type: actionTypes.SESSION_TIMER_TICK });
          };
        case true:
          return dispatch => {
            dispatch({ type: actionTypes.SESSION_TIMER_STOP });
          };
        default:
          console.log("startStopClickedHandler Session default triggered");
          break;
      }
    case "Break":
      switch (timerRunning) {
        case false:
          return dispatch => {
            const timerBreak = setInterval(
              () => dispatch({ type: actionTypes.BREAK_TIMER_TICK }),
              1000
            );
            dispatch({
              type: actionTypes.BREAK_TIMER_START,
              timerBreak: timerBreak
            });
            // dispatch({ type: actionTypes.BREAK_TIMER_TICK });
          };
        case true:
          return dispatch => {
            dispatch({ type: actionTypes.BREAK_TIMER_STOP });
          };
        default:
          console.log("startStopClickedHandler Break default triggered");
          break;
      }
    default:
      console.log("startStopClickedHandler default triggered");
      break;
  }
};
