import * as actionTypes from "./actionTypes";

export const timerReachesZeroHandlerForTimeLeft = displayText => {
  switch (displayText) {
    case "Session":
      console.log("Session timerReachesZeroHandlerForTimeLeft");
      return {
        type: actionTypes.SESSION_TIMER_REACHES_ZERO
      };
    case "Break":
      console.log("Break timerReachesZeroHandlerForTimeLeft");
      return {
        type: actionTypes.BREAK_TIMER_REACHES_ZERO
      };
    default:
      console.log("timerReachesZeroHandlerForTimeLeft default triggered");
      break;
  }
};
