import React from "react";
import styles from "./TimerSection.module.css";

import { connect } from "react-redux";

import * as actions from "../../store/actions/actions";

const TimerSection = props => {
  const timeDisplayFuction = timeLeftInS => {
    const timeLeftInMMNumber = Math.floor(timeLeftInS / 60);
    const timeLeftInSSNumber = timeLeftInS - timeLeftInMMNumber * 60;

    const timeLeftInMMString =
      timeLeftInMMNumber.toString().length === 1
        ? "0" + timeLeftInMMNumber.toString()
        : timeLeftInMMNumber.toString();
    const timeLeftInSSString =
      timeLeftInSSNumber.toString().length === 1
        ? "0" + timeLeftInSSNumber.toString()
        : timeLeftInSSNumber.toString();

    return timeLeftInMMString + ":" + timeLeftInSSString;
  };

  let displayTime = timeDisplayFuction(props.sessionTimeLeftInS);
  if (props.timerSessionDisplaying === false) {
    displayTime = timeDisplayFuction(props.breakTimeLeftInS);
  }

  if (displayTime === "00:01") {
    console.log(
      "!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!" +
        "  " +
        props.displayText +
        "  " +
        displayTime
    );
    props.timerReachesZeroHandlerForTimeLeft(props.displayText);
  }

  if (displayTime === "00:00") {
    document.getElementById("beep").play();
    switch (props.displayText) {
      case "Session":
        clearInterval(props.timerSession);
        clearInterval(props.timerBreak);
        break;
      case "Break":
        clearInterval(props.timerSession);
        clearInterval(props.timerBreak);
        break;
      default:
        console.log("startStopTimerClearInterval default triggered");
    }
    // props.timerReachesZeroHandlerForTimeLeft(props.displayText);
    setTimeout(
      () => props.timerReachesZeroHandlerForTimer(props.displayText),
      1000
    );
  }

  var d = new Date();
  var gt = d.getTime();
  console.log(props.displayText + "    " + displayTime + "    " + gt);

  return (
    <div className={styles.TimerSection}>
      <h3 id="timer-label" className={styles.h3}>
        {props.displayText}
      </h3>
      <p
        id="time-left"
        className={styles.p}
        // style={redText}
      >
        {displayTime}
      </p>
      <audio
        id="beep"
        src="http://soundbible.com/mp3/service-bell_daniel_simion.mp3"
        autoPlay={false}
      ></audio>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    breakTimeLeftInS: state.breakTimeLeftInS,
    sessionTimeLeftInS: state.sessionTimeLeftInS,
    timerSessionDisplaying: state.timerSessionDisplaying,
    displayText: state.displayText,
    timerSession: state.timerSession,
    timerBreak: state.timerBreak
  };
};

const mapDispatchToProps = dispatch => {
  return {
    timerReachesZeroHandlerForTimer: displayText =>
      dispatch(actions.timerReachesZeroHandlerForTimer(displayText)),
    timerReachesZeroHandlerForTimeLeft: displayText =>
      dispatch(actions.timerReachesZeroHandlerForTimeLeft(displayText))
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TimerSection);
