import * as actionTypes from "./actionTypes";

export const incrementClickedHandler = lengthID => {
  switch (true) {
    case lengthID === "break-length":
      return {
        type: actionTypes.INCREMENT_CLICKED,
        lengthName: "break"
      };
    case lengthID === "session-length":
      return {
        type: actionTypes.INCREMENT_CLICKED,
        lengthName: "session"
      };
    default:
      console.log("incrementClickedHandler default triggered");
  }
};
