import React from "react";
import styles from "./LengthSection.module.css";

import { connect } from "react-redux";

import * as actions from "../../store/actions/actions";

const LengthSection = props => {
  return (
    <div style={props.postion}>
      <h2 id={props.sectionID} className={styles.h3}>
        {props.sectionType}
      </h2>
      <div style={{ textAlign: "center" }}>
        <button
          id={props.decrementID}
          onClick={() => props.decrementClickedHandler(props.lengthID)}
        >
          &#8650;
        </button>
        <h3 id={props.lengthID}>{props.length}</h3>
        <button
          id={props.incrementID}
          onClick={() => props.incrementClickedHandler(props.lengthID)}
        >
          &#8648;
        </button>
      </div>
    </div>
  );
};

const mapDispatchToProps = dispatch => {
  return {
    decrementClickedHandler: lengthID =>
      dispatch(actions.decrementClickedHandler(lengthID)),
    incrementClickedHandler: lengthID =>
      dispatch(actions.incrementClickedHandler(lengthID))
  };
};

export default connect(
  null,
  mapDispatchToProps
)(LengthSection);
