import * as actionTypes from "../actions/actionTypes";

const initialState = {
  breakLength: 5,
  sessionLength: 25,
  breakTimeLeftInS: 300,
  sessionTimeLeftInS: 1500,
  // breakLength: 0.1,
  // sessionLength: 0.1,
  // breakTimeLeftInS: 6,
  // sessionTimeLeftInS: 6,
  timerRunning: false,
  timerSession: null,
  timerBreak: null,
  timerSessionDisplaying: true,
  displayText: "Session"
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.DECREMENT_CLICKED:
      switch (action.lengthName) {
        case "break":
          console.log("break DECREMENT_CLICKED");
          if (state.breakLength === 1) {
            return { ...state, breakLength: 1, breakTimeLeftInS: 60 };
          } else {
            return {
              ...state,
              breakLength: state.breakLength - 1,
              breakTimeLeftInS: (state.breakLength - 1) * 60
            };
          }
        case "session":
          console.log("session DECREMENT_CLICKED");
          if (state.sessionLength === 1) {
            return { ...state, sessionLength: 1, sessionTimeLeftInS: 60 };
          } else {
            return {
              ...state,
              sessionLength: state.sessionLength - 1,
              sessionTimeLeftInS: (state.sessionLength - 1) * 60
            };
          }
        default:
          return state;
      }

    case actionTypes.INCREMENT_CLICKED:
      switch (action.lengthName) {
        case "break":
          console.log("break INCREMENT_CLICKED");
          if (state.breakLength === 60) {
            return { ...state, breakLength: 60, breakTimeLeftInS: 3600 };
          } else {
            return {
              ...state,
              breakLength: state.breakLength + 1,
              breakTimeLeftInS: (state.breakLength + 1) * 60
            };
          }
        case "session":
          console.log("session INCREMENT_CLICKED");
          if (state.sessionLength === 60) {
            return { ...state, sessionLength: 60, sessionTimeLeftInS: 3600 };
          } else {
            return {
              ...state,
              sessionLength: state.sessionLength + 1,
              sessionTimeLeftInS: (state.sessionLength + 1) * 60
            };
          }
        default:
          return state;
      }

    case actionTypes.RESET_CLICKED:
      console.log("RESET_CLICKED");
      return {
        ...state,
        breakLength: 5,
        sessionLength: 25,
        breakTimeLeftInS: 300,
        sessionTimeLeftInS: 1500,
        timerRunning: false,
        timerSession: null,
        timerBreak: null,
        timerSessionDisplaying: true,
        displayText: "Session"
      };

    case actionTypes.SESSION_TIMER_START:
      console.log("SESSION_TIMER_START");
      return {
        ...state,
        timerRunning: true,
        timerSession: action.timerSession,
        timerBreak: null,
        timerSessionDisplaying: true,
        displayText: "Session"
      };

    case actionTypes.SESSION_TIMER_TICK:
      console.log("SESSION_TIMER_TICK");
      return {
        ...state,
        sessionTimeLeftInS: state.sessionTimeLeftInS - 1
      };

    case actionTypes.SESSION_TIMER_STOP:
      console.log("SESSION_TIMER_STOP");
      return {
        ...state,
        timerRunning: false
      };

    case actionTypes.SESSION_TIMER_REACHES_ZERO:
      console.log("SESSION_TIMER_REACHES_ZERO");
      return {
        ...state,
        breakTimeLeftInS: state.breakLength * 60
      };

    case actionTypes.BREAK_TIMER_START:
      console.log("BREAK_TIMER_START");
      return {
        ...state,
        timerRunning: true,
        timerBreak: action.timerBreak,
        timerSession: null,
        timerSessionDisplaying: false,
        displayText: "Break"
      };

    case actionTypes.BREAK_TIMER_TICK:
      console.log("BREAK_TIMER_TICK");
      return {
        ...state,
        breakTimeLeftInS: state.breakTimeLeftInS - 1
      };

    case actionTypes.BREAK_TIMER_STOP:
      console.log("BREAK_TIMER_STOP");
      return {
        ...state,
        timerRunning: false
      };

    case actionTypes.BREAK_TIMER_REACHES_ZERO:
      console.log("BREAK_TIMER_REACHES_ZERO");
      return {
        ...state,
        sessionTimeLeftInS: state.sessionLength * 60
      };

    default:
      return state;
  }
};

export default reducer;
