// https://learn.freecodecamp.org/front-end-libraries/front-end-libraries-projects/build-a-pomodoro-clock

import React, { Component } from "react";
import styles from "./ClockMain.module.css";

import LengthSection from "../LengthSection/LengthSection";
import TimerSection from "../TimerSection/TimerSection";
import TimerControls from "../TimerControls/TimerControls";

import { connect } from "react-redux";

class ClockMain extends Component {
  constructor(props) {
    super(props);

    this.sessionSetStateTimer = this.sessionSetStateTimer.bind(this);
    this.breakSetStateTimer = this.breakSetStateTimer.bind(this);
  }

  sessionSetStateTimer() {
    // console.log('sessionSetStateTimer starts')
    this.setState(() => {
      return { timerSessionDisplaying: true, displayText: "Session" };
    });

    let timerSession = setInterval(
      () =>
        this.setState(() => {
          if (this.state.sessionTimeLeftInS === 0) {
            clearInterval(this.state.timerSession);
            document.getElementById("beep").play();
            this.breakSetStateTimer();
            return {
              sessionTimeLeftInS: 0,
              breakTimeLeftInS: this.state.breakLength * 60
            };
          } else {
            return { sessionTimeLeftInS: this.state.sessionTimeLeftInS - 1 };
          }
        }),
      1000
    );
    this.setState({ timerSession: timerSession });
  }

  breakSetStateTimer() {
    // console.log('breakSetStateTimer starts')
    this.setState(() => {
      return { timerSessionDisplaying: false, displayText: "Break" };
    });

    let timerBreak = setInterval(
      () =>
        this.setState(() => {
          if (this.state.breakTimeLeftInS === 0) {
            clearInterval(this.state.timerBreak);
            document.getElementById("beep").play();
            this.sessionSetStateTimer();
            return {
              breakTimeLeftInS: 0,
              sessionTimeLeftInS: this.state.sessionLength * 60
            };
          } else {
            return { breakTimeLeftInS: this.state.breakTimeLeftInS - 1 };
          }
        }),
      1000
    );
    this.setState({ timerBreak: timerBreak });
  }

  render() {
    return (
      <div className={styles.ClockMain}>
        <h1 className={styles.h1}>Pomodoro Clock</h1>
        <LengthSection
          sectionType={"Break Length"}
          sectionID={"break-label"}
          decrementID={"break-decrement"}
          incrementID={"break-increment"}
          lengthID={"break-length"}
          postion={{ display: "inline-block" }}
          length={this.props.breakLength}
        />
        <LengthSection
          sectionType={"Session Length"}
          sectionID={"session-label"}
          decrementID={"session-decrement"}
          incrementID={"session-increment"}
          lengthID={"session-length"}
          postion={{ display: "inline-block", marginLeft: "50px" }}
          length={this.props.sessionLength}
        />
        <TimerSection />
        <TimerControls />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    breakLength: state.breakLength,
    sessionLength: state.sessionLength,
    breakTimeLeftInS: state.breakTimeLeftInS,
    sessionTimeLeftInS: state.sessionTimeLeftInS
  };
};

// const mapDispatchToProps = dispatch => {
//   return {
//     startTimer: () => {
//       const timerId = setInterval(() => dispatch({ type: TICK }), 10);
//       dispatch({ type: START_TIMER, timerId });
//     },
//     stopTimer: () => {
//       dispatch({ type: STOP_IT });
//     },
//     resetTimer: () => dispatch({ type: RESUME_IT })
//   };
// };

export default connect(
  mapStateToProps
  // , mapDispatchToProps
)(ClockMain);
