import * as actionTypes from "./actionTypes";

export const timerReachesZeroHandlerForTimer = displayText => {
  switch (displayText) {
    case "Session":
      console.log("Session timerReachesZeroHandlerForTimer");
      return dispatch => {
        const timerBreak = setInterval(
          () => dispatch({ type: actionTypes.BREAK_TIMER_TICK }),
          1000
        );
        dispatch({
          type: actionTypes.BREAK_TIMER_START,
          timerBreak: timerBreak
        });
      };
    case "Break":
      console.log("Break timerReachesZeroHandlerForTimer");
      return dispatch => {
        const timerSession = setInterval(
          () => dispatch({ type: actionTypes.SESSION_TIMER_TICK }),
          1000
        );
        dispatch({
          type: actionTypes.SESSION_TIMER_START,
          timerSession: timerSession
        });
      };
    default:
      console.log("timerReachesZeroHandlerForTimer default triggered");
      break;
  }
};
